  # 全局安装工具
  $ npm install -g zjport-cli
  \`\`\`
  \`\`\`shell
  # 创建项目
  $ zjport create 项目名
  \`\`\`
  \`\`\`shell
  # 查询命令文档
  $ zjport --help
  \`\`\`

